import { Fragment, FunctionalComponent, h } from 'preact';
import { Button } from './PrivacyButton.style';
import { DefaultUISettings } from '@usercentrics/cmp-browser-sdk';
import { PrivacyIcon } from '../../assets';
import { memo } from 'preact/compat';
import { useSettingsContext } from '../../contexts/SettingsContext';

type Props = {
  iconUrl: string | null;
  isEnabled: boolean;
  onClick: () => void;
  position: string;
  isMobile: boolean;
};

const PrivacyButton: FunctionalComponent<Props> = memo(({ iconUrl, isEnabled, isMobile, onClick, position }) => {
  const { settings } = useSettingsContext();
  const ui = settings.ui as DefaultUISettings;

  if (!isEnabled) {
    return <Fragment />;
  }

  return (
    <Button
      onClick={onClick}
      isMobile={isMobile}
      position={position}
      aria-label={ui.ariaLabels.privacyButton}
      data-testid="uc-privacy-button"
    >
      <PrivacyIcon iconUrl={iconUrl} />
    </Button>
  );
});

export default PrivacyButton;
